  position: relative;
  border-right: 1px solid var(--color-gray-600);
  box-sizing: border-box;
  width: 1px;
  flex-shrink: 0;
}
.company2 {
  position: relative;
  line-height: 27px;
  font-weight: 500;
}
.how-we-work2 {
  position: relative;
  display: inline-block;
  width: 197px;
}
.company-links2,
.company-section2,
.more-section2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.company-links2 {
  gap: var(--gap-xs);
  font-size: var(--font-size-base);
}
.company-section2,
.more-section2 {
  gap: var(--gap-2xl);
}
.footer-links2 {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  font-size: var(--font-size-xl);
}
.footer-section2,
.footer2,
.results-page {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.footer2 {
  align-self: stretch;
  background: linear-gradient(91.74deg, #0b5ba8 25.38%, #299bd8);
  flex-direction: row;
  padding: var(--padding-14xl) var(--padding-65xl);
  gap: var(--gap-25xl);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
.footer-section2,
.results-page {
  flex-direction: column;
}
.footer-section2 {
  align-self: stretch;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-cornflowerblue-200);
}
.results-page {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-42xl);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1200px) {
  .top-header2 {
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-21xl);
    box-sizing: border-box;
  }
  .search-filters {
    display: none;
  }
  .footer2,
  .search-results {
    padding: var(--padding-21xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1100px) {
  .navigation-menu2 {
    display: none;
  }
  .hamburger-menu-icon2 {
    display: flex;
  }
  .top-header2 {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
  }
  .where-are-you {
    font-size: var(--font-size-23xl);
    line-height: 48px;
  }
  .inputs-row1 {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
  .button-group1 {
    width: 100%;
    text-align: left;
  }
  .form-inputs-row1,
  .search-form1 {
    flex-direction: column;
  }
  .search-section {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
    width: 100%;
    top: calc(44% - 96.5px - 0px);
  }
  .airline-section {
    flex-direction: column;
  }
  .flight-cards {
    flex: unset;
    align-self: stretch;
  }
  .recently-booked {
    width: 100%;
  }
  .results-section1 {
    flex-direction: column;
    gap: 80;
  }
  .search-results {
    padding: var(--padding-xl) var(--padding-5xl);
    box-sizing: border-box;
  }
  .share-your-travels-form2 {
    padding: var(--padding-5xl) 0;
    box-sizing: border-box;
  }
  .fickleflight-bio2 {
    flex: unset;
    align-self: stretch;
  }
  .seperator2 {
    display: none;
  }
  .footer-links2 {
    flex: unset;
    align-self: stretch;
  }
  .footer2 {
    flex-direction: column;
    padding: var(--padding-21xl) var(--padding-5xl) var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 650px) {
  .navigation-right2 {
    display: flex;
  }
  .where-are-you {
    font-size: 31px;
    line-height: 40px;
  }
  .search-form-label1 {
    width: 100%;
    padding-bottom: var(--padding-3xs);
    margin-bottom: 6px;
    flex: unset;
    align-self: stretch;
  }
  .flight-options1 {
    width: 100%;
  }
  .form-title-group1 {
    flex-direction: column;
    align-items: flex-start;
  }
  .departure-autocomplete1 {
    flex: unset;
    align-self: stretch;
  }
  .inputs-row1 {
    flex-direction: column;
  }
  .search-button3 {
    width: 100%;
  }
  .search-form1 {
    margin-top: 20px;
  }
  .search-section {
    width: 100%;
    top: calc(32% - 96.5px - 0px);
  }
  .airline-section {
    flex-direction: row;
    width: 100% !important;
  }
  .flightduration {
    transform: scale(0.9);
  }
  .flight-details-section {
    flex: unset;
    align-self: stretch;
  }
  .separator-icon {
    display: none;
  }
  .price-section {
    width: auto;
    position: absolute;
    top: 30px;
    right: 18px;
  }
  .flight-card {
    flex-direction: column;
    gap: var(--gap-xl);
    padding: var(--padding-xl);
    box-sizing: border-box;
    position: relative;
  }
  .airline-icon1 {
    transform: scale(0.8);
  }
  .share-your-travels-form2 {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    position: relative !important;
    left: auto !important;
    width: 100%;
  }
  .company-section2 {
    margin-bottom: 40px !important;
  }
  .footer-links2 {
    flex-direction: column;
  }
  .footer2 {
    padding: var(--padding-11xl) var(--padding-5xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 350px) {
  .departure-airport,
  .departure-time {
    font-size: var(--font-size-base);
  }
  .departure-details {
    width: 80px;
  }
  .travel-time {
    font-size: var(--font-size-smi);
  }
  .arrival-details {
    width: 80px;
  }
}
